<template>
    <el-main>
        <el-row class="bg-grad-grey-reverse">
            <el-col class="padding">
                快速选择
            </el-col>
        </el-row>
        <el-row>
            <el-col class="padding">
                <el-button type="primary" round>
                    A211
                </el-button>
            </el-col>
        </el-row>
        <el-row class="bg-grad-grey-reverse">
            <el-col class="padding">
                精确选择
            </el-col>
        </el-row>
        <el-row style="display: flex;flex-wrap: wrap;-ms-flex-wrap: wrap;justify-content: space-between;">
            <el-col :span="6" class="padding" v-for="(item,key) in items" :key="key">
                <el-button type="primary" class="margin" round>
                    {{item.houseNum}}
                </el-button>
            </el-col>
        </el-row>
    </el-main>
</template>

<script>
  import axios from 'axios'
  import common from '@/assets/plus'

  export default {
    name: 'house',
    components: {
      axios, common
    },
    data () {
      return {
        items: []
      }
    },
    mounted: function () {
      let id = this.$route.params.id
      axios.request({
        url: common.HOST + '/public/get-house-list?bid=' + id,
        method: 'get'
      }).then((response) => {
        this.items = response.data.data
      }).catch((response) => {

      })
    },
    methods: {}
  }
</script>

<style scoped>

</style>



