<template>
    <el-main>
        <el-row>
            <el-col :span="24">
                <p class="bg-grad-grey-reverse padding radius padding-left">雅安</p>
            </el-col>
        </el-row>
        <el-row class="bg-grad-grey-reverse" type="flex" style="flex-wrap: wrap;justify-content: space-between">
            <el-col :span="5" style="margin: 10px;" v-for="(item,key) in data.yaan" :key="key">
                <router-link :to="{name:'house',params:{id:item.id}}" style="text-decoration: none">
                    <el-card class="box-card bg-grad-grass"
                             :body-style="{ 'padding': '10px','min-height':'200px','display':'flex','justifyContent':'center','alignItems':'center','background-color':''} ">
                        <!--                        <el-image src="https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png" fit="cover"></el-image>-->
                        <div>
                            {{item.buildingAlias}}
                        </div>
                    </el-card>
                </router-link>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="24">
                <p class="bg-grad-grey padding radius padding-left">成都</p>
            </el-col>
        </el-row>
        <el-row class="bg-grad-grey" type="flex" style="flex-wrap: wrap;justify-content: space-between">
            <el-col :span="5" style="margin: 10px;" v-for="(item,key) in data.chengdu" :key="key">
                <router-link :to="{name:'house',params:{id:item.id}}" style="text-decoration: none">
                    <el-card class="box-card bg-grad-grass"
                             :body-style="{ 'padding': '10px','min-height':'200px','display':'flex','justifyContent':'center','alignItems':'center','background-color':''} ">
                        <!--                        <el-image src="https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png" fit="cover"></el-image>-->
                        <div>
                            {{item.buildingAlias}}
                        </div>
                    </el-card>
                </router-link>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="24">
                <p class="bg-grad-grey padding radius padding-left">都江堰</p>
            </el-col>
        </el-row>
        <el-row class="bg-grad-grey" type="flex" style="flex-wrap: wrap;justify-content: space-between">
            <el-col :span="5" style="margin: 10px;" v-for="(item,key) in data.dujiangyan" :key="key">
                <router-link :to="{name:'house',params:{id:item.id}}" style="text-decoration: none">
                    <el-card class="box-card bg-grad-grass"
                             :body-style="{ 'padding': '10px','min-height':'200px','display':'flex','justifyContent':'center','alignItems':'center','background-color':''} ">
                        <!--                        <el-image src="https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png" fit="cover"></el-image>-->
                        <div>
                            {{item.buildingAlias}}
                        </div>
                    </el-card>
                </router-link>
            </el-col>
        </el-row>
    </el-main>
</template>

<script>
  import axios from 'axios'
  import '@/assets/public.css'
  import common from '@/assets/plus'

  export default {
    components: {
      axios
    },
    mounted: function () {
      axios.request({
        url: common.HOST + '/building/index?exp=' + JSON.stringify({
          city: '雅安'
        }),
        method: 'get'
      }).then((response) => {
        if (response.data.status === 0) {
          this.data.yaan = response.data.data
        }
      })
      axios.request({
        url: common.HOST + '/building/index?exp=' + JSON.stringify({
          city: '成都'
        }),
        method: 'get'
      }).then((response) => {
        if (response.data.status === 0) {
          this.data.chengdu = response.data.data
        }
      })
      axios.request({
        url: common.HOST + '/building/index?exp=' + JSON.stringify({
          city: '都江堰'
        }),
        method: 'get'
      }).then((response) => {
        if (response.data.status === 0) {
          this.data.dujiangyan = response.data.data
        }
      })
    },
    name: 'Index',
    data () {
      return {
        data: {
          yaan: {},
          chengdu: {},
          dujiangyan: {}
        }
      }
    },
    methods: {
      To (id) {
        this.$router.push({name: 'room', query: {id: id}})
      }
    }
  }
</script>

<style scoped>

    .box-card {
        font-size: 1.5rem;
        font-weight: bolder;
        border-radius: 4px;
    }
</style>



