import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/Index'
import house from '@/components/House'
import TimeTable from '@/components/TimeTable'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/house/:id/',
      name: 'house',
      component: house
    },
    {
      path: '/time/:id/',
      name: 'TimeTable',
      component: TimeTable
    }
  ]
})



