<template>
    <el-container class="bg-grad-grey-reverse">
        <el-main>
            <el-row>
                <el-col style="display: flex;justify-content: center">
                    <el-card>
                        <el-pagination
                                background
                                layout="prev, pager, next"
                                :total="200"
                                :current-page="currentWeeks"
                                @current-change="change_weekTimes"
                        >
                        </el-pagination>
                    </el-card>

                </el-col>
                <el-col style="display: flex;justify-content: center;" class="margin">
                    <el-card>
                        <div style="display: flex;justify-content: left;text-align: center;">
                            <div style="margin: 10px;width: 100px">&nbsp;</div>
                            <div style="margin: 10px;width: 100px;" v-for="item in ['一','二','三','四','五','六','日']">
                                周{{item}}
                            </div>
                        </div>
                        <div style="display: flex;text-align: center">
                            <div style="display: flex;flex-direction: column;margin: 10px;width: 100px;height: 1500px"
                                 class="bg-grad-grey-reverse radius">
                                <div v-for="i in 15" style="height: 100px;margin: 1px;">
                                    {{i+7}}:00
                                </div>
                            </div>
                            <div style="margin: 10px;height: 1500px;width: 100px;"
                                 v-for="(items,index) in data" :key="index">
                                <div :class="'bg-'+item.color" v-for="(item,key) in items" class="radius" :key="key"
                                     style="margin: 1px;display: flex;flex-direction: column;overflow: hidden"
                                     :style="{height:item.length*5/3+'px'}"
                                     @click="item.usage!==undefined?msg(item):mark(index,item)"
                                >
                                    <span class="text-bold text-xxs">
                                        {{8+parseInt(item.startHeight/60)+':'+parseInt(item.startHeight%60/10)+''+parseInt(item.startHeight%60%10)}}
                                    </span>
                                    <span class="text-bold text-xxs">{{item.usage!==undefined?'占用':'空闲'}}</span>
                                    <span class="text-bold text-xxs">
                                        {{8+parseInt(item.endHeight/60)+':'+parseInt(item.endHeight%60/10)+''+parseInt(item.endHeight%60%10)}}
                                    </span>

                                </div>
                            </div>
                        </div>
                    </el-card>
                </el-col>
            </el-row>
            <el-dialog :title="'开始预约'+info.houseNum" :visible.sync="status.mark_dialog">
                <el-form v-model="api" label-position="right">
                    <el-form-item :label="'第'+currentWeeks+'周' ">
                        <el-radio-group v-model="api.weekNum" :disabled="true">
                            <el-radio :value="1" :label="1">星期一</el-radio>
                            <el-radio :value="2" :label="2">星期二</el-radio>
                            <el-radio :value="3" :label="3">星期三</el-radio>
                            <el-radio :value="4" :label="4">星期四</el-radio>
                            <el-radio :value="5" :label="5">星期五</el-radio>
                            <el-radio :value="6" :label="6">星期六</el-radio>
                            <el-radio :value="7" :label="7">星期日</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="开始时间">
                        <el-time-picker
                                :picker-options="{selectableRange:api.range}"
                                v-model="api.startTime"
                                value-format="HH:mm:ss"
                        >
                        </el-time-picker>
                    </el-form-item>
                    <el-form-item label="结束时间">
                        <el-time-picker
                                :picker-options="{selectableRange:api.range}"
                                v-model="api.endTime"
                                value-format="HH:mm:ss"
                        >
                        </el-time-picker>
                    </el-form-item>
                    <el-form-item label="用于">
                        <el-input v-model="api.usage" style="width: 50%;min-width: 200px"></el-input>
                    </el-form-item>
                </el-form>
            </el-dialog>

        </el-main>
        <el-aside style="padding: 20px;">
            <el-row>
                <el-col>
                    <el-card class="box-card">
                        <div slot="header" class="clearfix">
                            <span>我预约的</span>
                        </div>
                        <div v-for="o in 4" :key="o" class="text item">
                            {{'列表内容 ' + o }}
                        </div>
                    </el-card>
                </el-col>
            </el-row>
        </el-aside>
    </el-container>
</template>

<script>
  import axios from 'axios'
  import '@/assets/color.css'
  import common from '@/assets/plus'

  export default {
    name: 'TimeTable',
    mounted () {
      this.info.id = this.$route.params.id
      this.refreshTable()
    },
    data () {
      return {
        info: {
          id: 0,
          houseNum: 'A211'
        },
        status: {
          mark_dialog: false
        },
        currentWeeks: 10,
        data: {},
        api: {
          weekTimes: ''
          , cid: ''
          , usage: ''
          , startTime: ''
          , endTime: ''
          , weekNum: 6
          , range: ''
        }
      }
    },
    methods: {
      msg (item) {
        this.$alert('<p class="text-bold">开始时间：' + item.startTime + '</p><p class="text-bold">用于：' + item.usage + '</p><p class="text-bold">结束时间：' + item.endTime + '</p>', '占用信息', {
          dangerouslyUseHTMLString: true
        })
      },
      mark (key, item) {
        this.status.mark_dialog = true
        this.api.weekNum = parseInt(key)
        this.api.range = new Date(2019, 10, 10, 8 + item.startHeight / 60, item.startHeight % 60 / 10).Format('hh:mm:ss') + ' - ' + new Date(2019, 10, 10, 8 + item.endHeight / 60, item.endHeight % 60 / 10).Format('hh:mm:ss')
      },
      refreshTable () {
        axios.request({
          url: common.HOST + '/public/get-time-table?cid=' + this.info.id + '&weekTimes=' + this.currentWeeks,
          method: 'get'
        }).then((response) => {
          this.data = format_time_table(response.data.data, 870)
        })
      },
      change_weekTimes (t) {
        this.currentWeeks = t
        this.refreshTable()
      }
    }
  }

  function format_time_table (data, limitTime) {
    let newData = {
      '1': [],
      '2': [],
      '3': [],
      '4': [],
      '5': [],
      '6': [],
      '7': []
    }
    for (let i in data) {
      switch (data[i].weekNum) {
        case 0:
          newData['1'].push(data[i])
          break
        case 1:
          newData['2'].push(data[i])
          break
        case 2:
          newData['3'].push(data[i])
          break
        case 3:
          newData['4'].push(data[i])
          break
        case 4:
          newData['5'].push(data[i])
          break
        case 5:
          newData['6'].push(data[i])
          break
        case 6:
          newData['7'].push(data[i])
          break
      }
    }
    for (let i in newData) {
      //初始化
      let start = 0,
        end = 0,
        line = []
      if (newData[i].length === 0) {
        line.push({
          startHeight: end,
          endHeight: limitTime,
          length: limitTime - end,
          color: getColor()
        })
        newData[i] = line
        continue
      }
      for (let k in newData[i]) {
        let startHei = parseInt(newData[i][k].startHeight),
          endHei = parseInt(newData[i][k].endHeight)
        if (startHei > 0 && startHei - end - 1 > 0) {
          let tmp = {
            startHeight: end,
            endHeight: startHei,
            length: startHei - end,
            color: getColor(),
          }
          line.push(tmp)
          line.push(newData[i][k])
        } else {
          line.push(newData[i][k])
        }
        start = startHei
        end = endHei
      }
      if (limitTime - end + 1 > 0) {
        line.push({
          startHeight: end,
          endHeight: limitTime,
          length: limitTime - end - line.length,
          color: getColor(),
        })
      }
      newData[i] = line
    }
    console.log(newData)
    return newData
  }

  function getColor () {
    var color = ['red', 'green', 'blue', 'orange', 'olive', 'cyan', 'purple', 'mauve', 'pink', 'brown',
      'yellow'
    ]
    var index = parseInt(Math.random() * 10)
    return color[index]
  }

</script>

<style scoped>

</style>



